:root {
    --color-white-100: #FFFFFF;
    --color-black-900: #000000;
    --color-font-secondary: orangered;
    --color-font-secondary-plus: #000552;
    --color-minimalist-100: #dbdbdb;
    --color-minimalist-300: #a7c8ee;
    --color-minimalist-500: #57a3fa;
    --color-minimalist-700: #0073f7;
}

.card-container{
    padding: 2rem;
    position: relative;
    max-width: 350px;
    min-height: 500px;
    font-family: "DM Sans", sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
}

.card-container>img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    z-index: 1;
}

.upper-section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
    color: var(--color-font-secondary);
}

.upper-section>div{
    font-size: 2rem;
    line-height: 1;
    z-index: 2;
}

.middle-section{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: flex-start;
    align-items: flex-start;
    z-index: 2;
    color: var(--color-font-secondary-plus);
}

.middle-headline{
    font-size: 2rem;
    line-height: 1;
    z-index: 2;
}

.middle-description{
    font-size: 1rem;
    line-height: 1;
    z-index: 2;
    color: var(--color-font-secondary);
}

.bottom-section{
    color: var(--color-font-secondary);
    font-size: 2rem;
    line-height: 1;
    z-index: 2;
}

@media screen and (max-width: 420px){
    .card-container{
        padding: 1rem;
        position: relative;
        max-width: 250px;
        min-height: 350px;
        font-family: "DM Sans", sans-serif;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 2rem;
    }

    .upper-section>div{
        font-size: clamp(1rem, 4vw + .2rem, 2rem);
        line-height: 1;
        z-index: 2;
    }

    .bottom-section{
        color: var(--color-font-secondary);
        font-size: clamp(1rem, 6vw + .2rem, 2rem);
        line-height: 1;
        z-index: 2;
    }
    
}