:root{
    --color-white-100: #FFFFFF;
    --color-black-900: #000000;
    --color-minimalist-100: #F8F8F8;
    --color-minimalist-200: #EBEBEB;
    --color-minimalist-300: #DADADA;
    --color-minimalist-400: #BBBBBB;
    --color-minimalist-500: #9B9B9B;
    --color-minimalist-600: #707070;
    --color-minimalist-700: #595959;
    --color-minimalist-800: #404040;
    --color-minimalist-900: #1E1E1E;
}

.outer-container{
    overflow: hidden;
}

.hero-section{
    position: relative;
    width: 100%;
    height: 150vh;
    background-image: url('../../../../public/optimized-images/ecommerce/project-1-background.webp');
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
}

.hero-section::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(transparent 0, transparent 30%, black 100%); 
    z-index: 1;
}

.hero-text{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    z-index: 2;
}

.hero-left{
   flex: 1;
   padding: 2rem;
}

.hero-right{
   flex: 1;
}

.hero-left>h1{
    /*font-family: "PT Sans Narrow", sans-serif;*/
    font-family: "PT Sans", sans-serif;
    /*font-family: "Oswald", sans-serif;*/
    font-size: clamp(1.75rem, 4vw + 1rem, 4rem);
    font-weight: 500;
    text-transform: lowercase;
    line-height: 1;
    color: var(--color-white-100);
    text-shadow: 10px 10px 50px #ffffff;
    /*text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);*/
    margin-bottom: 0;
    padding-bottom: 0;
}

.hero-span{
    display: flex;
}

.hero-span>span{
    color: dodgerblue;
    text-shadow: 10px 10px 50px #198AFC;
    /*filter: drop-shadow(1px 1px 10px #14598b);*/
    font-size: clamp(2rem, 10vw + 1rem, 8rem);
    text-transform: uppercase;
    letter-spacing: clamp(.5rem, 2vw + .1rem, 2rem);
}

.hero-left>h3{
    font-family: "PT Sans Narrow", sans-serif;
    font-size: clamp(1rem, 2vw + 1rem, 1.5rem);
    font-weight: 500;
    text-transform: capitalize;
    line-height: 1;
    color: var(--color-white-100);
    /*text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);*/
}

.hero-left>div>a>button{
    margin-top: 1rem;
}

.banner-ads{
    margin-block: 0rem;
    width: 100%;
    background-color: rgb(25, 118, 210);
    color: var(--color-white-100);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    padding-block: .5rem;
}

.banner-ads>h3{
    font-family: "PT Sans", sans-serif;
    font-weight: lighter;
    margin: 0;
}

.banner-ads>h3>span{
    color: var(--color-black-900);
    font-weight: bold;
    font-style: italic;
    text-decoration: underline;
}

.video-section{
    height: 100vh;
    position: relative;
}

.video-section::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(transparent 0, transparent 10%, black 100%); 
    z-index: 1;
}

.video-section>video{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.scroll-down-container{
    position: absolute;
    top: 98%;
    left: 2%;
    transform: translate(-2%, -98%);
    width: 70px;
    height: 70px;
    z-index: 2;
}

.scroll-down-inner-container{
    position: relative;
    width: 100%;
    height: 100%;
}

.scroll-down{
    position: absolute;
    top: 100%;
    left: 0;
    transform: translate(0%, -100%);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px dashed var(--color-white-100);
    padding: 1rem;
    opacity: 0.7;
    animation: spin 20s linear infinite;
}

@keyframes spin {
    from{
      transform: translate(-2%, -98%) rotate(0deg);
    }
    to{
      transform: translate(-2%, -98%) rotate(-360deg); 
    }
}
  
.middle-section{
    position: relative;
    padding: clamp(6rem, 20vh + .5rem, 10rem) 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    /*background-color: rgb(25, 118, 210);*/
    /*background-color: rgb(9, 9, 10);*/
    /*background-image: linear-gradient(to bottom, whitesmoke, dodgerblue, rgb(0, 80, 160)); */
}

