.in-between{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}

.vertical-line-top{
    width: 1px;
    height: clamp(100px, 20vw + 16px, 200px);
    background-color: dodgerblue;

}

.small-text{
    font-size: clamp(.8rem, 2vw + .1rem, 1rem);
    line-height: 1;
    color: var(--color-black-900);
    text-transform: uppercase;
    padding-bottom: 1rem;
    border-bottom: 1px solid dodgerblue;
    text-align: center;
}

.large-text{
    font-family: "PT Sans", sans-serif;
    font-weight: 700;
    font-size: clamp(1.5rem, 4vw + .2rem, 3rem);
    line-height: 1;
    color: rgb(0, 42, 83);
    text-align: center;
}

.vertical-line-bottom{
    margin-bottom: 4rem;
    width: 1px;
    height: clamp(130px, 25vw + 16px, 300px);
    background-color: dodgerblue;
}

.paragraph-section{
    margin-inline: clamp(1rem, 4vw + .1rem, 5rem);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: clamp(2rem, 4vw + .1rem, 4rem);
}

.paragraph-section>p{
    flex: 1;
    font-family: "DM Sans", sans-serif;
    font-weight: 300;
    font-size: clamp(.8rem, 2vw + .2rem, 1.2rem);
    color: var(--color-black-900);
    text-align: justify;
}

.paragraph-section>p>span{
    font-family: "PT Sans", sans-serif;
    font-style: italic;
    font-weight: 700;
    /*color: var(--color-black-900);*/
    color: rgb(0, 42, 83);
}

.middle-section>h2{
    padding-inline: 2rem;
    font-family: "DM Sans", sans-serif;
    font-size: clamp(1.5rem, 2vw + 1rem, 2rem);
    font-weight: 300;
    text-transform: lowercase;
    line-height: 1;
    color: var(--color-black-900);
    text-align: center;
}

.middle-section>h2>span{
    font-style: italic;
    font-weight: 700;
}

.middle-section>span{
    margin-top: 2rem;
    width: 6rem;
    height: 6rem;
    color: dodgerblue;
    border: 1px dashed dodgerblue;
    border-radius: 50%;
}

.middle-section>span>*{
    width: 100%;
    height: 100%;
}

.canvas-section{

}

.middle-bottom-section{
    margin: 5rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.middle-bottom-section>h2{
    font-family: "PT Sans", sans-serif;
    font-size: clamp(1.5rem, 2vw + 1rem, 2.5rem);
    font-weight: 200;
    text-transform: uppercase;
    line-height: 1;
    text-align: center;
    color: var(--color-black-900);
}

.middle-bottom-section>h2>span{
    color: dodgerblue;
    font-weight: 700;
}

.middle-bottom-section>h4{
    font-family: "PT Sans", sans-serif;
    font-size: clamp(1rem, 1vw + .2rem, 1.5rem);
    font-weight: 500;
    text-transform: lowercase;
    line-height: 1;
    color: var(--color-black-900);
    padding-bottom: 1rem;
    border-bottom: 1px solid dodgerblue;
}

.cards-section{
    padding: 2rem;
}

.cards-group{
    display: flex;
    gap: 1rem;
}

.cards-group:nth-child(2){
    margin-top: 1rem;
}

.cards-group>*{
    flex: 1;
    margin-top: 3rem;
}

.cards-group:nth-child(2)>*{
    margin-top: 1rem;
}

@media(max-width: 560px){
    .hero-section{
        height: 100vh;
    }

    .hero-left>h1{
        font-size: clamp(.5rem, 2.5vw + .2rem, 1.5rem);
    }

    .hero-left>h3{
        font-size: clamp(.5rem, 1vw + 1rem, 1rem);
    }

    .middle-section{
        padding: 3rem 1rem 4rem 1rem;
    }

    .middle-bottom-section{
        margin: 3rem 2rem;
    }

    .paragraph-section{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }

    .middle-section>h2{
        font-size: clamp(1rem, 2vw + .5rem, 1.5rem);
    }

    .middle-section>span{
        margin-top: 1rem;
        width: 4rem;
        height: 4rem;
    }

    .cards-group{
        flex-direction: column;
    }

    .cards-group>*{
        margin-top: 1rem;
    }

    .cards-group:nth-child(1){
        margin-top: 2rem;
    }
}