:root {
    --color-white-100: #FFFFFF;
    --color-black-900: #000000;
    --color-font-secondary: orangered;
    --color-font-secondary-plus: #000552;
    --color-minimalist-100: #dbdbdb;
    --color-minimalist-300: #a7c8ee;
    --color-minimalist-500: #57a3fa;
    --color-minimalist-700: #0073f7;
    --padding-inline: clamp(1rem, 4vw + .5rem, 10rem);
    --padding-block: clamp(4rem, 2vh + 1rem, 6rem);
}

.reviews-container{
    font-family: "DM Sans", sans-serif;
    background-color: var(--color-font-secondary);
}

.reviews-inner-container{
    padding: 8rem 10rem;
    position: relative;
}

.slider-container{
    margin-inline: 10rem;
    overflow: hidden;
}

.arrow-left-btn{
    position: absolute;
    top: 50%;
    left: 15rem;
    transform: translate(0, -50%);
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    background-color: var(--color-minimalist-100);
}

.arrow-left-btn:hover, .arrow-left-btn:focus{
    cursor: pointer;
}

.arrow-left-inner-container{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: transparent;
    color: var(--color-font-secondary);
    display: flex;
    justify-content: center;
    align-items: center;
}

.slider-wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    transition: all 1.5s ease;
}

.slide{
    padding-inline: 2rem;
    padding-bottom: 1rem;
    min-width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    position: relative;
}

.slide-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.customer-area{
   font-size: 1.2rem;
}

.stars-area{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: .5rem;
}

.star-container{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-minimalist-100);
    color: var(--color-font-secondary-plus);
}

.slide-middle{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.headline-area{
    font-size: 2rem;
    font-style: italic;
    text-align: center;
}

.review-area{
    text-align: center;
}

.review-area>p{

}

.slide-bottom{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.date-area{
    text-align: center;
}

.arrow-right-btn{
    position: absolute;
    top: 50%;
    right: 15rem;
    transform: translate(0, -50%);
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    background-color: var(--color-minimalist-100);
}

.arrow-right-btn:hover, .arrow-right-btn:focus{
    cursor: pointer;
}

.arrow-right-inner-container{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: transparent;
    color: var(--color-font-secondary);
    display: flex;
    justify-content: center;
    align-items: center;
}

.small-horizontal-line{
    width: 50px;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: var(--color-black-900);
    opacity: 0;
}

@media screen and (max-width: 1200px){
    .reviews-inner-container{
        padding: var(--padding-block) var(--padding-inline);
        position: relative;
    }
    
    .slider-container{
        margin-inline: var(--padding-inline);
        overflow: hidden;
    }

    .arrow-left-btn{
        position: absolute;
        top: 50%;
        left: clamp(.5rem, 4vw + .1rem, 10rem);
        transform: translate(0, -50%);
        width: 30px;
        height: 30px;
        border: none;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 2;
        background-color: var(--color-minimalist-100);
    }

    .arrow-right-btn{
        position: absolute;
        top: 50%;
        right: clamp(.5rem, 4vw + .1rem, 10rem);
        transform: translate(0, -50%);
        width: 30px;
        height: 30px;
        border: none;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 2;
        background-color: var(--color-minimalist-100);
    }
    
    .slide{
        padding-inline: 0rem;
        padding-bottom: 1rem;
        min-width: 100%;
        min-height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 2rem;
        position: relative;
    }
    
    .slide-top{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }

    .stars-area{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: .1rem;
    }

}